import React from 'react'
import style from './Post.module.css'
import postCreatorPhoto from './../../../../../images/profile-big-photo.jpg'
import postPhotosUrl from './../../../../../images/post-large-1.jpg'

const Post = props => {
    return (
        <div className={style.post}>
            <div className={style.postHeader}>
                <img src={ props.userSmallPhoto ? props.userSmallPhoto : postCreatorPhoto} className={style.postCreatorPhoto}/>
                <div className={style.postInfo}>
                    <p className={style.user}>{props.postAuthor}</p>
                    <p className={style.postTime}></p>
                </div>
            </div>
            <p className={style.postText}>{props.postText}</p>
            <div className={style.postPhotos}>
                <img src={ postPhotosUrl } className={style.postPhotosItem}/>
            </div>
        </div>
    )
}

export default Post;
