import React, { useState } from 'react'
import style from './HomeNav.module.css'
import bootstrap from './../../../commonStyleModules/bootstrap.module.css'
import navPhotolink from './../../../images/profile-big-photo.jpg'
import { NavLink } from 'react-router-dom';
import PhotoModal from '../../common/Modal/PhotoModal';


const HomeNav = props => {

    const [editMode, setEditMode] = useState(false);

    const activateEditMode = () => {
        setEditMode(true)
    }

    const deactivateEditMode = () => {
        setEditMode(false)
    }

    return (
        <React.Fragment>
            <PhotoModal
                editMode = {editMode}
                deactivateEditMode = {deactivateEditMode}
                updateUserPhoto = {props.updateUserPhoto}
                closeModal = {deactivateEditMode}
                isFetching = {props.isFetching}
            />
            <div className={style.homeNavWrap}>
                <div className={bootstrap.container}>
                    <div className={style.homeNav}>
                        <div className={style.homeNavPhoto} onDoubleClick={ props.isOwner ? activateEditMode : null}>
                            <img src={props.profile && props.profile.photos.large ? props.profile.photos.large : navPhotolink} />
                        </div>
                        <nav className={style.nav}>
                            <ul className={style.navList}>
                                <div className={style.space} />
                                <li><NavLink to='/home/profile' className={style.homeNavItem} activeClassName={style.active}>Profile</NavLink></li>
                                <li><NavLink to={'#'} className={style.homeNavItem} activeClassName={style.active}>Timeline</NavLink></li>
                                <li><NavLink to={'#'} className={style.homeNavItem} activeClassName={style.active}>About</NavLink></li>
                                <li><NavLink to={'#'} className={style.homeNavItem} activeClassName={style.active}>Photos</NavLink></li>
                                <li><NavLink to='/home/users' className={style.homeNavItem} activeClassName={style.active}>Users</NavLink></li>
                                <li><NavLink to={'#'} className={style.homeNavItem} activeClassName={style.active}>More</NavLink></li>
                            </ul>
                        </nav>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default HomeNav;
