import React from 'react'
import Home from './Home';
import { connect } from 'react-redux';
import { authentication, logout } from './../../redux/auth-reducer';
import { updateUserPhoto } from './../../redux/profile-reducer';
import { withAuthRedirect } from './../redirect/withAuthRedirect';


class HomeContainer extends React.Component{

    render(){
        return(
            <Home
                profile = {this.props.profile} 
                isAuth = {this.props.isAuth}
                login = {this.props.login}
                logout = {this.props.logout}
                isFetching = {this.props.isFetching}
                isOwner = {this.props.isOwner}
                
                updateUserPhoto = {this.props.updateUserPhoto}
                defineOwner = {this.props.defineOwner}
            />
        )
    }
}

const mapStateToProps = state => ({
    profile: state.profilePage.profile,
    login: state.auth.login,
    isAuth: state.auth.isAuth,
    isFetching: state.profilePage.isFetching,
    isOwner: state.profilePage.isOwner,
})

let withRedirectHomeContainer = withAuthRedirect(HomeContainer)

export default connect(mapStateToProps, {
    authentication,
    logout,
    updateUserPhoto,
} )(withRedirectHomeContainer)