import React from 'react'
import style from './HomeHeader.module.css'
import logoImgUrl from './../../../images/logo.png'
import profSmallPhoto from './../../../images/profile-small.jpg'
import { NavLink } from 'react-router-dom';
import bootstrap from './../../../commonStyleModules/bootstrap.module.css'


const ProfileHeader = props => {

    const logout = () => {
        props.logout()
    }

    return (
        <div className={style.headerWrap}>
            <div className={bootstrap.container}>
                <div className={style.header}>
                    <ul className={style.headerBtnBlock}>
                        <li><NavLink className={style.headerBtnItem} to={'/'}>Home</NavLink></li>
                        <li><NavLink className={style.headerBtnItem} to={'/'}>Message</NavLink></li>
                        <li><NavLink className={style.headerBtnItem} to={'/'}>Notification</NavLink></li>
                    </ul>
                    <div className={style.headerLogo}>
                        <NavLink to={'/news'}><img src={logoImgUrl} /></NavLink>
                    </div>
                    <div className={style.searchBlock}>
                        <div className={style.loginBlock}>
                            {props.isAuth
                                ? <div><p className={style.greeting}>Hey, <span>{props.login}</span></p><button className={style.authBtn} onClick={logout}>Log Out</button></div>
                                : <NavLink to={'/login'}><button className={style.authBtn}>Login</button></NavLink>
                            }
                        </div>
                        <img
                            src={profSmallPhoto}
                            className={style.profSmallPhoto}
                        />
                    </div>
                </div>
            </div>

        </div>
    )
}

export default ProfileHeader