import { usersAPI } from './../api/api';

const TOGGLE_FOLLOW = 'TOOGLE_FOLLOW';
const SET_USERS = 'SET_USERS';
const CHANGE_CURRENT_PAGE = 'CHANGE_CURRENT_PAGE';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOOGLE_NAV_BUTTONS = 'TOOGLE_NAV_BUTTONS';


const initialState = {
    users: [],
    pageSize: 16,
    currentPage: 1,
    totalUsersCount: 60,
    isFetching: false,
    isNavButtonsOpened: false,
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case TOGGLE_FOLLOW:
            return {
                ...state,
                users: state.users.map(elem => {
                    if(elem.id === action.userId){
                        return {
                            ...elem,
                            followed: !elem.followed
                        }
                    }
                    return elem;
                })
            };
        case SET_USERS:
            return{ ...state, users: action.users };
        case CHANGE_CURRENT_PAGE:
            return{ ...state, currentPage: action.pageNumber, };
        case TOGGLE_IS_FETCHING:
            return{ ...state, isFetching: action.value };
        case TOOGLE_NAV_BUTTONS:
            return{ ...state, isNavButtonsOpened: action.value };
        default:
            return state;
    };
};

const toggleFollow = userId => ({type: TOGGLE_FOLLOW, userId});
const setUsers = users => ({type: SET_USERS, users});
const changeCurrentPage = pageNumber => ({type: CHANGE_CURRENT_PAGE, pageNumber });
const toggleIsFetching = value => ({type: TOGGLE_IS_FETCHING, value});
const toggleNavButtons = value => ({type: TOOGLE_NAV_BUTTONS, value});


export const getUsers = (pageSize, pageNumber) => async dispatch => {
    dispatch(toggleIsFetching(true));
    dispatch(changeCurrentPage(pageNumber));
    let response = await usersAPI.getUsers(pageSize, pageNumber);
    dispatch(toggleIsFetching(false));
    dispatch(setUsers(response));
    dispatch(toggleNavButtons(true));
};

export const follow = userId => async dispatch => {
    let response = await usersAPI.followUser(userId);
    if(response.resultCode === 0) dispatch(toggleFollow(userId));
};

export const unfollow = userId => async dispatch => {
    let response = await usersAPI.unfollowUser(userId);
    if(response.resultCode === 0) dispatch(toggleFollow(userId));
};


export default usersReducer;