import React from 'react'
import style from './HomeDropdown.module.css'
import { NavLink } from 'react-router-dom';

const HomeDropdown = () => {
    return (
        <div className={style.homeDropdown}>
                <ul className={`${style.menuHeader} ${style.homeDropdownItem}`}>
                    <li className={style.menuHeaderName}>Madison Howard</li>
                    <li className={style.menuHeaderEmail}><NavLink className={style.menuHeaderEmailLink} to={'mailnam@mail.com'}>mailnam@mail.com</NavLink></li>
                </ul>
                <ul className={`${style.menuBody} ${style.homeDropdownItem}`}>
                    <li><NavLink className={style.menuLink} to={'/'}>Profile</NavLink></li>
                    <li><NavLink className={style.menuLink} to={'/'}>Inbox</NavLink></li>
                    <li><NavLink className={style.menuLink} to={'/'}>Activity</NavLink></li>
                </ul>
                <ul className={`${style.menuFooter} ${style.homeDropdownItem}`}>
                    <li><NavLink className={style.menuLink} to={'/'}>Settings</NavLink></li>
                    <li><NavLink className={style.menuLink} to={'/'}>Sing out</NavLink></li>
                </ul>
            </div>
    )
}

export default HomeDropdown;
