import React from 'react';
import Profile from './Profile';
import { connect } from 'react-redux';
import { addNewPost, getProfile, getUserStatus, updateUserStatus, defineOwner } from './../../../redux/profile-reducer';
import { withRouter, Redirect } from 'react-router-dom';
import { compose } from 'redux';
import { withAuthRedirect } from './../../redirect/withAuthRedirect';


class ProfileContainer extends React.Component{

    refreshProfile = (dispatch) => {
        let userId = this.props.match.params.userId;
        if(!userId){
            userId = this.props.authorizedUserId;
            if(!userId){
                this.props.history.push('/login');
            }
        } 
        this.props.getProfile(userId, this.props.match.params.userId ? false : true);
        this.props.getUserStatus(userId);
        this.props.defineOwner(!this.props.match.params.userId)
    }
    
    componentDidMount(){
        this.refreshProfile()
    }

    componentDidUpdate(prevProps){
        if(this.props.match.params.userId !== prevProps.match.params.userId){
            this.refreshProfile()
        }
    }

    render(){
        return(
            <Profile 
                profile = {this.props.profile}
                posts = {this.props.posts}
                status = {this.props.status}
                userSmallPhoto = {this.props.userSmallPhoto}
                
                addNewPost = {this.props.addNewPost}
                updateUserStatus = {this.props.updateUserStatus}
            />
        )
    }
}

const mapStateToProps = state => ({
    profile: state.profilePage.profile,
    posts: state.profilePage.posts,
    authorizedUserId: state.auth.userId,
    status: state.profilePage.status,
    userSmallPhoto: state.profilePage.profile ? state.profilePage.profile.photos.small : null,
})

let withRouterProfileContainer = withRouter(ProfileContainer)

export default connect(mapStateToProps, {
    addNewPost,
    getProfile,
    getUserStatus,
    updateUserStatus,
    defineOwner,
})(withRouterProfileContainer)

