import React from 'react'
import style from './User.module.css'
import userImgUrl from './../../../../../images/friends/friends-1.jpg'
import { NavLink } from 'react-router-dom'


const User = props => {

    const toggleFollow = () => {
        if (props.userFollowed) {
            props.unfollow(props.userId)
        } else {
            props.follow(props.userId)
        }
    }

    return (
        <div className={style.usersItem}>
            <NavLink to={`/home/profile/${props.userId}`}>
                <img
                    src={props.smallPhoto ? props.smallPhoto : userImgUrl}
                    className={style.usersImg}
                />
            </NavLink>
            <div className={style.usersContent}>
                <NavLink to={`/home/profile/${props.userId}`}><div className={style.usersName}>{props.userName}</div></NavLink>
                <button
                    className={style.usersFollowBtn}
                    onClick={toggleFollow}
                >{props.userFollowed ? 'Unfollow' : 'Follow'}</button>
            </div>
        </div>
    )
}

export default User
