import React from 'react'
import style from './About.module.css'
import sidebarComponent from './../../../../../commonStyleModules/sidebarComponent.module.css'
import Status from './Status/Status'


const About = props => {
    return (
        <div className={sidebarComponent.component}>
            <div className={sidebarComponent.title}>
                <p>{props.profile.fullName}</p>
            </div>
            <Status 
                status = {props.status}
                updateUserStatus = {props.updateUserStatus}
            />
            <ul className={style.info}>
                <li>
                    <p className={style.infoItem}>
                        <i className="fas fa-briefcase"></i>
                        {props.profile.lookingForAJob ? 'Looking for a job' : "Working"}
                    </p>
                </li>
                <li>
                    <p className={style.infoItem}>
                    <i className="fas fa-map-marker-alt"></i>
                        Melborne, Australia
                    </p>
                </li>
                <li>
                    <p className={style.infoItem}>
                    <i className="far fa-heart"></i>
                        Travel, Swimming
                    </p>
                </li>
            </ul>
        </div>
    )
}

export default About;
