import * as axios from 'axios'


const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        "API-KEY": "774b2d99-e0fe-4222-9700-7f568947563e",
    },
});


export const usersAPI = {
    async getUsers(pageSize, pageNumber){
        let response = await instance.get(`users?count=${pageSize}&page=${pageNumber}`);
        return response.data.items;
    },
    async followUser(userId){
        let response = await instance.post(`follow/${userId}`);
        return response.data;
    },
    async unfollowUser(userId){
        let response = await instance.delete(`follow/${userId}`);
        return response.data;
    },
};

export const profileAPI = {
    async getUserProfile(userId){
        let response = await instance.get(`profile/${userId}`);
        return response.data;
    },
    async getStatus(userId){
        let response = await instance.get(`profile/status/${userId}`);
        return response.data;
    },
    async updateStatus(newStatus){
        let response = await instance.put('profile/status', {status: newStatus});debugger
        return response.data;
    },
    async updatePhoto(photoFile){
        let formData = new FormData();
        formData.append('image', photoFile)
        let response = await instance.put('profile/photo', formData, {
            headers: {
                'Content-Type' : 'multipart/form-data'
            }
        });
        return response.data;
    }
};

export const authAPI = {
    async authMe(){
        let response = await instance.get('auth/me');
        return response.data;
    },
    async login(email, password){
        let response = await instance.post('/auth/login', { email, password });
        return response.data;
    },
    async logout(){
        let response = await instance.delete('/auth/login');
        return response.data;
    }
};