import { authAPI } from './../api/api';
import { stopSubmit } from "redux-form";


const SET_AUTH_USER_DATA = 'SET_AUTH_USER_DATA';
const TOGGLE_FETCHING = 'TOGGLE_FETCHING';


const initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    isFetching: false,
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA:
            return{ ...state, ...action.authData };
        case TOGGLE_FETCHING:
            return {...state, isFetching: action.value}
        default:
            return state;
    }
}


const setAuthUserData = (userId, email, login, isAuth) => ({type: SET_AUTH_USER_DATA, authData: {userId, email, login, isAuth} });
const toggleFetching = value => ({type: TOGGLE_FETCHING, value})


export const authentication = () => async dispatch => {
    let response = await authAPI.authMe();
    if(response.resultCode === 0){
        let { id, email, login } = response.data;
        dispatch(setAuthUserData( id, email, login, true));
    }
};

export const login = (email, password) => async dispatch => {
    dispatch(toggleFetching(true));
    let response = await authAPI.login(email, password);
    if(response.resultCode === 0){
        await dispatch(authentication())
        dispatch(toggleFetching(false));
    }else{
        let message = response.messages.length > 0 ? response.messages[0] : "Some error"
        dispatch(stopSubmit('login', { _error: message }))
    }
};

export const logout = () => async dispatch => {
    dispatch(toggleFetching(true));
    let response = await authAPI.logout();
    dispatch(toggleFetching(false));
    if(response.resultCode === 0){
        dispatch(setAuthUserData( null, null, null, false));
    }
};


export default authReducer;