import React from 'react'
import style from './CreatePost.module.css'
import userPhotoUrl from './../../../../../images/profile-big-photo.jpg'
import CreatePostReduxForm from './CreatePostReduxForm/CreatePostReduxForm';

const CreatePost = props => {

    const addNewPost = values => {
        props.addNewPost(values.newPostText)
    }

    return (
        <div className={style.createPostWrap}>
            <img src={ props.userSmallPhoto ? props.userSmallPhoto : userPhotoUrl } className={style.userPhoto}/>
            <CreatePostReduxForm onSubmit={addNewPost}/> 
        </div>
    )
}

export default CreatePost;
