import React from 'react'
import style from './Friends.module.css'
import sidebarComponent from './../../../../../commonStyleModules/sidebarComponent.module.css'
import friendsImg_1 from './../../../../../images/friends/friends-1.jpg'
import friendsImg_2 from './../../../../../images/friends/friends-2.jpg'
import friendsImg_3 from './../../../../../images/friends/friends-3.jpg'
import friendsImg_4 from './../../../../../images/friends/friends-4.jpg'
import friendsImg_5 from './../../../../../images/friends/friends-5.jpg'
import friendsImg_6 from './../../../../../images/friends/friends-6.jpg'


const Friends = () => {
    return (
        <div className={sidebarComponent.component}>
            <div className={sidebarComponent.title}>
                <p>Friends Zone</p>
            </div>
            <div className={style.friendsItem}>
                <img src={friendsImg_1} className={style.friendsImg}/>
                <p className={style.friendsName}>Arfim Bolt</p>
            </div>
            <div className={style.friendsItem}>
                <img src={friendsImg_2} className={style.friendsImg}/>
                <p className={style.friendsName}>Marry Wither</p>
            </div>
            <div className={style.friendsItem}>
                <img src={friendsImg_3} className={style.friendsImg}/>
                <p className={style.friendsName}>Rolin Theitar</p>
            </div>
            <div className={style.friendsItem}>
                <img src={friendsImg_4} className={style.friendsImg}/>
                <p className={style.friendsName}>Active Mind</p>
            </div>
            <div className={style.friendsItem}>
                <img src={friendsImg_5} className={style.friendsImg}/>
                <p className={style.friendsName}>William Henry</p>
            </div>
            <div className={style.friendsItem}>
                <img src={friendsImg_6} className={style.friendsImg}/>
                <p className={style.friendsName}>Robart Faul</p>
            </div>
        </div>
    )
}

export default Friends
