import React from 'react'
import style from './CreatePostReduxForm.module.css'
import { reduxForm, Field } from 'redux-form';
import { required, maxLenghtCreator } from '../../../../../../utils/validator';
import { Textarea } from './../../../../../FormControl/FormControl';


const maxLength = maxLenghtCreator(20)

const CreatePostForm = props => {

    const onSubmitForm = () => {
        props.handleSubmit();
        // props.reset();
    }

    return (
        <form className={style.postForm} onSubmit={props.handleSubmit} >
            <Field
                component={Textarea}
                name={'newPostText'}
                className={style.postTextarea}
                placeholder='Say something'
                validate={[required, maxLength]}
            />
            <button className={style.postBtn}>share</button>
        </form>
    )
}

const CreatePostReduxForm = reduxForm({ form: 'createPostForm' })(CreatePostForm)

export default CreatePostReduxForm
