import React, { useState } from 'react';
import Modal from 'react-modal'
import style from './PhotoModal.module.css'
import CirclePreloader from '../../preloader/CirclePreloader';

Modal.setAppElement('#root')

const PhotoModal = props => {

    const [selectedPhoto, setSelectedPhoto] = useState(null);

    const selectPhoto = event => {
        if (event.target.files.length) {
            setSelectedPhoto(event.target.files[0])
        }
    }

    const onChangePhoto = () => {
        props.updateUserPhoto(selectedPhoto);
        setSelectedPhoto(null);
        // props.deactivateEditMode();
    }

    return (
        <Modal
            isOpen={props.editMode}
            className={style.PhotoModal_Content}
            overlayClassName={style.PhotoModal_Overlay}
            onRequestClose={props.deactivateEditMode}
        >
            {props.isFetching ? <CirclePreloader/> : null}
            <p className={style.photoTitle} >If you want to change your current profile photo, you can upload another one</p>
            <div className={style.inputBlock}>
                <input type="file" name="file" id="file" className={style.photoInput} onChange={selectPhoto} />
                <label htmlFor="file" className={style.photoLabel}>{selectedPhoto ? '1 file selected' : 'Choose a file'}</label>
            </div>
            {selectedPhoto && <button className={style.photoButton} onClick={onChangePhoto}>Set photo</button>}
        </Modal>
    )
}

export default PhotoModal
