import React from 'react'
import style from './UsersList.module.css'
import User from './User/User'
import CirclePreloader from './../../../preloader/CirclePreloader';
import PaginationControlled from './PaginationControlled';


const UsersList = props => {

    const getUsersList = () => {
        return (
            props.users.map(elem => {
                return (
                    <User
                        key={elem.id}
                        userId={elem.id}
                        userName={elem.name}
                        smallPhoto={elem.photos.small}
                        userFollowed={elem.followed}

                        follow = {props.follow}
                        unfollow = {props.unfollow}
                    />
                )
            })
        )
    }

    // const getNavButtons = () => {
    //     let navButtons = []
    //     let pageCount = Math.ceil(props.totalUsersCount / props.pageSize)

    //     for (let i = 1; i <= pageCount; i++) {
    //         navButtons.push(
    //             <li
    //                 className={i === props.currentPage ? `${style.navButtonsItem} ${style.active}` : style.navButtonsItem}
    //                 onClick={() => { props.changePage(i) }}
    //                 key = {i}
    //             >
    //                 {i}
    //             </li>
    //         )
    //     }
    //     return navButtons;
    // }

    return (
        <div className = {style.usersListWrap}>
            {props.isFetching ? <CirclePreloader/> : null}
            <ul className={style.usersList}>
                {getUsersList()}
            </ul>
            <div className={style.userListNav}>
                { props.isNavButtonsOpened &&
                <PaginationControlled
                    totalUsersCount = {props.totalUsersCount}
                    currentPage = {props.currentPage}
                    changePage = {props.changePage}
                />}
                { !props.isNavButtonsOpened && null }
            </div>
        </div>
    )
}

export default UsersList;