import React from 'react'
import style from './UsersNav.module.css'
import btnStyle from './../../../../commonStyleModules/buttonStyles.module.css'


const UsersNav = () => {
    return (
        <div className={style.usersNavWrap}>
            <div className={style.usersCount}>Users (<span>125</span>)</div>
            <ul className={style.usersNav}>
                <li className={style.NavItem}><div className={btnStyle.btnType_1}>All</div></li>
                <li className={style.NavItem}><div className={btnStyle.btnType_1}>Online</div></li> 
            </ul>
        </div>
    )
}

export default UsersNav
