import { profileAPI } from "../api/api"
import { reset } from "redux-form";

const CHANGE_NEW_POST_TEXT = 'CHANGE_NEW_POST_TEXT';
const ADD_NEW_POST = 'ADD_NEW_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS';
const SET_USER_PHOTO = 'SET_USER_PHOTO';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const DEFINE_OWNER = 'DEFINE_OWNER';


const initialState = {
    profile: null,
    isOwner: true,
    status: '',
    posts: [
        { id: 1, author: 'Kate Palson', text: 'Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for "lorem ipsum" will uncover many web sites still in their infancy.' },
    ],
    isFetching: false,
    isOwner: true,
}

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case CHANGE_NEW_POST_TEXT:
            return { ...state, newPostText: action.text };
        case ADD_NEW_POST:
            return { ...state, posts: [...state.posts, { id: 4, author: 'Kate Palson', text: action.newPostText }], };
        case SET_USER_PROFILE:
            return { ...state, profile: action.data };
        case SET_USER_STATUS:
            return { ...state, status: action.status };
        case SET_USER_PHOTO: debugger
            return { ...state, profile: { ...state.profile, photos: action.photos } };
        case TOGGLE_IS_FETCHING:
            return { ...state, isFetching: action.value };
        case DEFINE_OWNER:
            return { ...state, isOwner: action.value }
        default:
            return state;
    }
}

// export const addNewPost = newPostText => ({ type: ADD_NEW_POST, newPostText });
export const addNewPost = newPostText => dispatch => {    
    dispatch({type: ADD_NEW_POST, newPostText})
    dispatch(reset('createPostForm'));
}

const setUserProfile = data => ({ type: SET_USER_PROFILE, data });
const setUserStatus = status => ({ type: SET_USER_STATUS, status });
const setUserPhoto = photos => ({ type: SET_USER_PHOTO, photos })
const toggleIsFetching = value => ({ type: TOGGLE_IS_FETCHING, value });
export const defineOwner = value => ({type: DEFINE_OWNER, value});


export const getProfile = (userId) => async dispatch => {
    let response = await profileAPI.getUserProfile(userId);
    dispatch(setUserProfile(response));
};

export const getUserStatus = userId => async dispatch => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setUserStatus(response));
};

export const updateUserStatus = status => async dispatch => {
    let response = await profileAPI.updateStatus(status);
    if (response.resultCode === 0) {
        dispatch(setUserStatus(status));
    }
};

export const updateUserPhoto = photoFile => async dispatch => {
    dispatch(toggleIsFetching(true))
    let response = await profileAPI.updatePhoto(photoFile);
    if (response.resultCode === 0) {
        dispatch(toggleIsFetching(false))
        dispatch(setUserPhoto(response.data.photos))
    }
}


export default profileReducer;